@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Nunito', sans-serif;
    background-color: #1a1a1c;
    color: #fff;
}

.coin-app {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 64px;
    color: #fff;
  }
  
  .coin-search {
    margin-bottom: 64px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
  }
  
  .coin-text {
    margin-bottom: 32px;
    margin-top: 20px;
    text-align: center;
  }
  
  .coin-input {
    padding-left: 16px;
    width: 300px;
    height: 50px;
    border-radius: 4px;
    border: none;
    outline: none;
    background-image: linear-gradient(
      -225deg,
      #740000 0%,
      #fcff2e 48%,
      #9c9e04 100%
    );
  
    color: #e2e2e2;
  }
  
  .coin-input::placeholder {
    color: #e2e2e2;
  }